<template>
  <PopupLayout
      :title="'일괄알람설정'"
      :popupClass="['popup_box','alarm_popup_box']"
      :isOpen="isOpen"
      @onClose="onAllAlarmPopupClose"      
  >
   <div class="popup_bd">
      
      <div class="popup_left_box">
          <div class="inner">

              <div class="alarm_row">

                  <span class="alarm_row_label main_label">알람</span>
                  <RadioBox 
                      v-model="params.alarAt"
                      :items="itemsAlarmAt"
                  ></RadioBox>

              </div>
              <!-- // 알람 ON 항목 -->
              <div class="alarm_row alarm_off_obj"  v-show="!isAlarm">

                  <span class="alarm_row_label">반복</span>
                  <RadioBox 
                      type='v'
                      v-model="params.alarCycleCode"
                      :items="itemsAlarCycleCode"
                  ></RadioBox>

              </div>
              <div class="alarm_row alarm_off_obj" v-show="!isAlarm && ['W'].includes(inAlarCycleCode)">
                  <span class="alarm_row_label">요일</span>
                  <check-box
                    v-model="params.reptitDotwNm"
                    :items="itemsReptitDotwNm"
                    :_class="['input_group chk_group dis_ib chk_week']"
                    :labelClass="['input_box chk_box']"
                  ></check-box>
              </div>
              <div class="alarm_row" v-if="isAlarm || ['N'].includes(inAlarCycleCode)">
                  <span class="alarm_row_label">시작</span>
  
                  <div class="input_group datetime">
                    <!-- <input type="text" class="input_txt input_date" v-model="startDateInput" @click="fnOpen('startDate')"> -->

                    <datepicker 
                        :language="lang" 
                        :format="customFormatter" 
                        v-model="params.beginDe"
                        class="input_box dev_dt ml0" 
                        :style="{'fontSize':'16px'}"
                        ref="startDatePicker"
                        :inline = "false"
                        calendar-button-icon="btn-calendar"
                        :calendar-button="false"
                        placeholder="시작일">
                    </datepicker>
<!--                    <input-hm divClass="input_box time" v-model="params.beginHm" /> -->
                      <select-hm v-model="params.beginHm" />

                          
                </div> 
  
                  
              </div>
              <div class="alarm_row"  v-if="!isAlarm && ['W','D'].includes(inAlarCycleCode)">
                  <span class="alarm_row_label">시작시간</span>
                  
                    <div class="input_group datetime">
                      <!-- <input-hm divClass="input_box time ml2" v-model="params.beginHm" /> -->
                      <select-hm v-model="params.beginHm" />
                  </div>
              </div>

              <div class="alarm_row"  v-if="isAlarm || ['N'].includes(inAlarCycleCode)">
                  <span class="alarm_row_label">종료</span>
                  <div class="input_group datetime">
                      <datepicker 
                            :language="lang" 
                            :format="customFormatter" 
                            v-model="params.endDe"
                            class="input_box dev_dt ml0" 
                            style="font-size:16px"
                            ref="startDatePicker"
                            :inline = "false"
                            calendar-button-icon="btn-calendar"
                            :calendar-button="false"
                            placeholder="종료일">
                        </datepicker>
                        <select-hm v-model="params.endHm" />
                  </div>

              </div>
              <div class="alarm_row"  v-if="!isAlarm && ['W','D'].includes(inAlarCycleCode)">
                  <span class="alarm_row_label">종료시간</span>
                  <div class="input_group datetime">
                    
                      <select-hm v-model="params.endHm" />
                  </div>
              </div>

              <div class="alarm_row alarm_off_obj" v-show="!isAlarm">
                  <span class="alarm_row_label">검지유형</span>
                  <check-box
                    
                    v-model="params.scanSeNm"
                    :items="itemsScanSeNm"
                    :labelClass="['input_box chk_box mt0 mb0 mr10']"
                  ></check-box>

              </div>
              <!-- 
                  알람 ON 선택시 클래스명 alarm_on_obj 를 display: block
                  알람 OFF 선택시 클래스명 alarm_off_obj 를 display: block
                -->
                <!-- 알람 ON 항목 -->
              <div class="alarm_row alarm_on_obj" v-show="isAlarm">

                  <span class="alarm_row_label wide_label">예외검지구분</span>
                  <check-box
                    v-model="params.exceptScanSeNm"
                    :items="itemsScanSeNm"
                  ></check-box>

              </div>
              <div class="alarm_row alarm_on_obj" v-show="isAlarm">

                  <span class="alarm_row_label wide_label">예외시간대</span>
                  <check-box
                    v-model="params.exceptTm"
                    :items="itemsExceptTm"
                  ></check-box>

              </div>
                <!-- // 알람 ON 항목 -->

              <div class="alarm_confirm_obj">
                  <p class="alarm_alert_txt alarm_on_obj" v-show="isAlarm">*알람이벤트시 알람예외상태를 선택하세요.</p>
                  <p class="alarm_alert_txt alarm_off_obj" v-show="!isAlarm">*제외하고 싶은 CCTV의 경우 제외버튼을 클릭해주세요</p>
                  <div class="input_group txt_c">
                      <div class="input_box dis_ib">
                          <button class="btn btn_md btn_primary" @click="onConfirm">확인</button>
                      </div>
                      <div class="input_box dis_ib ml5">
                          <button class="btn btn_md btn_primary_outline" @click="onCancel">취소</button>
                      </div>
                  </div>
              </div>

          </div>
      </div>

      <div class="popup_right_box">

          <!-- 이벤트이력 -->
          <!-- 헤더고정 테이블의 경우 헤더와 바디 두개의 테이블의 colgroup 선언을 동일하게 해주세요. -->
          <!-- 헤더고정테이블의 2개 줄이 필요한 경우가 있습니다. 그럴땐 tr 에 클래스명 table_hd_category를 추가해주세요. -->
          <DefaultTable
            :tblClass="['fixed_table_box', 'alarm_table']"
            :headers="hCctvList"
            :items="itemsCctv"
            @cellClick="onCellClick"
          ></DefaultTable>
          
          <!-- 이벤트이력 -->

      </div>

  </div>
  </PopupLayout>
</template>
<script>
  
  import {mapActions} from 'vuex'
  import PopupLayout from '@/components/cctv/PopupLayout'
  import CheckBox from '@/components/cctv/common/CheckBox'
  import RadioBox from '@/components/cctv/common/RadioBox'
  import InputHm from '@/components/cctv/common/InputHm'
  import DefaultTable from '@/components/cctv/DefaultTable'
  import {ko ,en} from 'vuejs-datepicker/dist/locale';
  import Datepicker from 'vuejs-datepicker'
  import dayjs from 'dayjs'
  import SelectBox from '@/components/common/SelectBox.vue'
  import SelectHm from '@/components/cctv/common/SelectHm.vue'

  export default {
    name: 'PopupAlarm',
    components: {
      PopupLayout,
      CheckBox,
      RadioBox,
      DefaultTable,
      InputHm,
      Datepicker,
      SelectBox,
      SelectHm
    },
    props: {
      isOpen: false,
      cctv_id: '',
      title: '',
      popupClass: {
        type: Array,
        default: () => {
          return ['popup_box']
        }
      },
      isOpen: Boolean,
      items:[]
    },
    computed: {
      p () {
        return this.params.endHm
      },
      isAlarm() {
        return this.params.alarAt == 'N'? false:  true
      },
      inAlarCycleCode() {
        return this.params.alarCycleCode
      }
    },
    data() {
      return {
        lang:ko,
        itemsAlarmAt:[ //알람상태
          {id:'N', text:'OFF'}, 
          {id:'Y', text:'ON'}
        ],
        itemsAlarCycleCode: [
          {id:'N', text: '반복안함'},
          {id:'D', text: '일마다'},
          {id:'W', text: '주마다'}
        ],
        // 검지유형 ScanTyCode
        itemsScanSeNm: [
          {id:'001', text:'정지차량'},
          {id:'002', text:'역주행'},
          {id:'003', text:'보행자'}
        ],
        itemsExceptTm: [
          {id:'T01', text:'주간'},
          {id:'T02', text:'야간'},
          {id:'T03', text:'일출'},
          {id:'T04', text:'일몰'}
        ],
        itemsReptitDotwNm: [
          {id:'mon', text:'월요일'},
          {id:'tue', text:'화요일'},
          {id:'wed', text:'수요일'},
          {id:'thu', text:'목요일'},
          {id:'fri', text:'금요일'},
          {id:'sat', text:'토요일'},
          {id:'sun', text:'일요일'},
        ],
        beginDe:dayjs(new Date()).format("YYYY-MM-DD"),
        beginHm:'1234',
        endHm:'0000',
        params: {
          alarAt: 'N',         //알람 ON/OFF
          alarCycleCode: 'N',  //알람 구분 코드  'D', 'W'
          scanSeNm:[],         //검지구분명,
          reptitDotwNm: [],    //반복요일명
          exceptScanSeNm:[],   //예외검지유형
          exceptTm: [],        //예외시간대
          beginDe: dayjs(new Date()).format("YYYY-MM-DD"),          //시작일자
          beginHm: dayjs(new Date()).format("HHmm"),          //시작시분
          endDe:dayjs(new Date()).format("YYYY-MM-DD"),            //종료일자
          endHm:dayjs(new Date()).format("HHmm"),            //종료시분
        },
        hCctvList: [
          {id:"CCTV_ID", name:"CCTV ID", cols_class:"w19p", cell_template: this.getTemplateId},
          {id:"INSTL_LC_ABRV", name:"설치위치", cols_class:"w29p"},
          {id:"NONE1", name:"화질", cols_class:"w14p"},
          {id:"act", name:"선택", cols_class:"w23p", type:'Button', label:'제외'},
        ],
        itemsCctv: this.items
      }
    },
    methods: {
      ...mapActions('cctv', ['postAlarmOff']),
      customFormatter(date)
      {
          // 달력 형식 지정 
          return dayjs(date).format('YYYY-MM-DD');
      },
      onAllAlarmPopupClose(){
        this.$emit("onClose", false)
      },
      getTemplateId(row, v) {
        return `<a href="javascript:void(0)">${v}</a>`
      },
      onCellClick(row, v) {
        if(v == 'act') {
          // 제외 처리
          this.itemsCctv = this.itemsCctv.filter(item => {
            return item.CCTV_ID != row.CCTV_ID
          })
        } else if (v == 'CCTV_ID' && $(event.currentTarget).find("span")) {
            // CCTV 관리 팝업 오픈
            // this.onAllAlarmPopupClose()
            this.$parent.onMangerPopup(row.CCTV_ID)
          
        }
      },
      // this.params 초기화
      initParams() {
        if(this.isAlarm) {
          this.params = {
            ...this.params,
            alarCycleCode: '',   //알람 구분 코드  'D', 'W'
            scanSeNm:[],         //검지구분명,
            reptitDotwNm: [],    //반복요일명
          }
        } else {
          this.params = {
            ...this.params,
            exceptScanSeNm:[],   //예외검지유형
            exceptTm: [],        //예외시간대
          }
          
        }
      },
      async onConfirm() {
        
        this.initParams()

        if(!this.isAlarm) {

          if(this.params.alarCycleCode == 'N') {
            if(!/[0-9]{4}[0-9]{2}[0-9]{2}/.test(dayjs(this.params.beginDe).format("YYYYMMDD"))) {
              this.$info('시작일시를 선택해주세요')
              return false
            }
            if(!/[0-9]{4}[0-9]{2}[0-9]{2}/.test(dayjs(this.params.endDe).format("YYYYMMDD"))) {
              this.$info('종료일시를 선택해주세요')
              return false
            }
          }
          if(['W','D','N'].includes(this.params.alarCycleCode)) {
            if(!this.params.beginHm) {
              this.$info('시작시간을 선택해주세요')
              return false
            }
            if(!this.params.endHm) {
              this.$info('종료시간을 선택해주세요')
              return false
            }
          }
          if(['W'].includes(this.params.alarCycleCode)) {
            if(this.params.reptitDotwNm.length <= 0) {
              this.$info('요일을 선택해주세요')
              return false
            }
          }

          if (this.params.scanSeNm.length <= 0){
            this.$info('검지유형을 선택해주세요')
            return false
          }
        
        } else {
          if(this.params.exceptScanSeNm.length <= 0) {
            this.$info('예외 검지구분을 1개 이상 선택해주세요')
            return false
          }
          if(this.params.exceptTm.length <=0){
            this.$info('예외 시간대를 1개 이상 선택해주세요')
            return false
          }
        }


        const chk = await this.$confirm('정말로 등록하시겠습니까?')
        if(chk) {
          const cctvIds = this.itemsCctv.map(item => { 
            return item.CCTV_ID

            })
          const params = {
              ...this.params,
              beginDe: ['D','W'].includes(this.params.alarCycleCode) && !this.isAlarm ?'': dayjs(this.params.beginDe).format("YYYYMMDD"),
              endDe: ['D','W'].includes(this.params.alarCycleCode) && !this.isAlarm ? '': dayjs(this.params.endDe).format("YYYYMMDD"),
              reptitDotwNm: ['W'].includes(this.params.alarCycleCode) && !this.isAlarm ? this.params.reptitDotwNm: [],
              cctvIds:cctvIds
            }
          
          this.postAlarmOff(params).then(res => {
            this.$info('알람이 등록되었습니다')

          })
        }
      },
      onCancel() {
        this.onAllAlarmPopupClose()
      },
      onChangeHm(key) {
        const v = event.target.value.replace(/[^0-9]/g,"")
        const vv = v.slice(0,4)
        
        this.params[key] = vv.replace(/([0-9]{2})([0-9]{2})/,"$1:$2")
      }
    },
    watch: {
      items(v) {
        this.itemsCctv = [...v]
      },
    }
  }
</script>
<style>
  .vdp-datepicker.input_box.dev_dt input { 
    width:100%; 
    height:30px;
    font-size:14px;
    color:#666;
    padding-left: 15px;
  }

</style>
<style scoped>
  .alarm_confirm_obj {
    z-index:1
  }
</style>


